.gm_area .gm_inner {
  position: relative;
}

.gm_area .gm_nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.gm_area .gm_nav > .gm_nav_item > .gm_item_name {
  display: block;
}

.gm_area .gm_nav .gm_accordion {
  position: relative;
}
.gm_area .gm_nav .gm_accordion .gm_accordion_body {
  position: absolute;
  top: 90%;
  width: 220px;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
}
.gm_area .gm_nav .gm_accordion .gm_accordion_body li a {
  display: block;
}
.gm_area .gm_nav .gm_accordion:last-child .gm_accordion_body {
  right: 0;
}
.gm_area.is_preloaded .gm_nav .gm_accordion .gm_accordion_body {
  -webkit-transition: opacity 0.2s, top 0.2s, visibility 0.2s;
  transition: opacity 0.2s, top 0.2s, visibility 0.2s;
}
.gm_area.is_preloaded .gm_nav .gm_accordion.is_show .gm_accordion_body {
  top: 100%;
  opacity: 1;
  visibility: visible;
}

.gm_area .gm_mega .gm_mega_body {
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
}
.gm_area.is_preloaded .gm_mega .gm_mega_body {
  -webkit-transform: scale(0.9) perspective(1000);
          transform: scale(0.9) perspective(1000);
  -webkit-transform-origin: top center;
          transform-origin: top center;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  -webkit-transition: opacity 0.2s visibility 0.2s;
  transition: opacity 0.2s visibility 0.2s;
}
.gm_area.is_preloaded .gm_mega.is_show .gm_mega_body {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.gm_area .gm_inner.is_fixed {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  z-index: 1;
}